.StatHeader_wrapper {
  background-color: #fff;
  border-bottom: 1px solid #e8e8e8;
  padding: 12px 0;

  .header {
    display: flex;
    margin: 0 auto;
    height: 25px;
    .left {
      flex: 1;
      h1 {
        font-size: 18px;
        line-height: 1;
      }
    }
    .right {
      flex: 1;
      text-align: right;
    }
    .main {
      flex: 1;
      text-align: center;
      height: 25px;
    }
  }
}
