.Stat_container {
  display: flex;
  flex-direction: column;
  background-color: #f0f2f5;
  min-height: 100vh;
  .content_wrapper {
    flex: auto;
    padding: 12px 0;
    .content {
      margin: 0 auto;
      display: flex;
      .left {
        width: 350px;
        margin-right: 24px;
        padding: 12px;
        background-color: #fff;
      }
      .main {
        flex: auto;
        background-color: #fff;
        padding: 12px 18px;
      }
      .right {
        width: 400px;
        margin-left: 24px;
        background-color: #fff;
        padding: 12px 18px;
        overflow: hidden;
      }
    }
  }
}
